import { MessageBox,Navbar, TabItem,Indicator, Actionsheet ,Toast } from 'mint-ui';
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);
var config = require('../../../config')
import {
    mapState
} from 'vuex';
import { getuserinfo } from '../../assets/js/common_function.js';
    export default {
        data(){

            return {
                 product:[],
                 userinfo:[],
                isfocus:false
            }
        },
          watch: {
            // 如果 question 发生改变，这个函数就会运行
            question: function (newQuestion) {

            }
          },
          components:{

            },
           mounted: function() {
                var userid=    this.$route.query.userid;
                var obj3 = new Object();
                obj3.u_id =userid;
                this.getuserinfo(obj3);
            },
            methods:{
                unfocus(u_id,e){
                    var u_id =u_id;
                    var token = window.storeWithExpiration.get('token');
                    var el =e;
                    var dataupdate=[];
                    var _that=this;
                    var data=[];
                    axios.get( '/user/unuserfoucs', {
                        params: {
                            token: token,
                            u_id: u_id
                        }
                    }).then(function(response) {
                        if(response.status=='200'){
                            return response.data;
                        }
                    }).then(function(json) {
                        var message ='取消关注成功';
                        if(4000==json.code){
                            message='取消关注失败';
                        }
                        var userid=    _that.$route.query.userid;
                        var obj3 = new Object();
                        obj3.u_id =userid;
                        _that.getuserinfo(obj3);
                        Toast({
                            message:message,
                            iconClass: 'mintui mintui-success'
                        });
                    }).catch(function(ex) {
                        Toast({
                            message: '取消关注失败',
                            iconClass: 'mintui mintui-success'
                        });
                    });
                },
                focus(u_id,e){
                    var token = window.storeWithExpiration.get('token');
                    var el =e;
                    var dataupdate=[];
                    var _that=this;
                    var data=[];
                    axios.get( '/user/userfoucs', {
                        params: {
                            token: token,
                            u_id: u_id
                        }
                    }).then(function(response) {
                        if(response.status=='200'){
                            return response.data;
                        }
                    }).then(function(json) {
                        var message ='关注成功';
                        if(4000==json.code){
                            message='已经关注';
                        }else if(2000==json.code){
                            _that.$store.commit('upfoucs', json);
                        }
                        var userid=    _that.$route.query.userid;
                        var obj3 = new Object();
                        obj3.u_id =userid;
                        _that.getuserinfo(obj3);
                        Toast({
                            message:message,
                            iconClass: 'mintui mintui-success'
                        });

                    }).catch(function(ex) {
                        Toast({
                            message: '关注失败',
                            iconClass: 'mintui mintui-success'
                        });
                    });
                },
                getuserinfo(obj){
                var	that =this;

                    Indicator.open({
                        text: '正在加载，请稍后...',
                        spinnerType: 'fading-circle'
                    });
               axios.get( '/product/getuserall', {
                    params: {
                        token: storeWithExpiration.get('token'),
                        u_id: obj.u_id
                    }
                }) .then(function(response) {
                        if(response.status=='200'){
                            return response.data;
                        }
                    }).then(function(json) {

                        if(typeof(json.user_info)!='undefined'){
                            that.product = json.product;
                            that.userinfo = json.user_info;
                            if(1==json.isfocus){
                                that.isfocus = true;
                            }else{
                                that.isfocus = false;
                            }
                            Indicator.close();
                        }
                }).catch(function(ex) {
                    console.log(ex);
                   Indicator.close();
                });

                }
            },
            computed:{

            }
    }



